
html, body {
  background-color: #101011;
  margin: 0px;
  padding: 0px;
  overflow-x:hidden;
}
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #E8E7DD;
  margin-top: 60px;
}

#maincont {
    max-width: 800px;
    margin: auto;
    font-size: 19px;
    margin-bottom: 10px;
}
#name {
    font-size: 22px;
}
#age:hover {
    cursor: pointer;
}
#lang_detail {
    margin-top: 10px;
    margin-bottom: 30px;
}
#footer {
    font-size: 15px;
}
@media only screen and (max-width: 600px) {
#name {
        font-size: 15px;
}
}
a {
    text-decoration: none;
    font-size: 17px;
    color: #E8E7DD;
}
a:hover {
    cursor: pointer;
    color: #66ff66;
}
.flexc {
    margin: auto;
    max-width: 650px;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-orient: horizontal;
    -webkit-box-direction: normal;
        -ms-flex-direction: row;
            flex-direction: row;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
    -webkit-box-pack: center;
        -ms-flex-pack: center;
            justify-content: center;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
}
fieldset {
    margin: auto;
    max-width: 300px;
    border-color: #aaaaaa;
    border-width:1px;
}
.language {
    -webkit-box-flex: 1;
        -ms-flex-positive: 1;
            flex-grow: 1;
}
.text {
    font-family: 'Courier New', Courier, monospace;
}
.red {
    color: #fc4c58;
}
.green {
    color: #66ff66;
}
.dim {
    color: #aaaaaa;
}
.show-enter-active {
  -webkit-transition: all .3s ease;
  transition: all .3s ease;
}
.show-leave-active {
  -webkit-transition: all .8s cubic-bezier(1.0, 0.5, 0.8, 1.0);
  transition: all .8s cubic-bezier(1.0, 0.5, 0.8, 1.0);
}
.show-enter, .show-leave-to
/* .slide-fade-leave-active below version 2.1.8 */ {
  -webkit-transform: translateX(10px);
          transform: translateX(10px);
  opacity: 0;
}
::-moz-selection {
  color: white;
  background: #fc4c58;
}
::selection {
  color: white;
  background: #fc4c58;
}

